<template>
    <v-container fluid>
        <v-layout fill-height>
            <v-flex >
                    <btnBar :prog_cd="prog"
                            @btnAction = btnAction>
                    </btnBar>
                    <v-alert
                        type="warning"
                        :value="isFail"
                        dismissible
                        transition="scale-transition">
                        No seledted Item!
                    </v-alert>
                    <v-data-table
                            v-model="selected"
                            :headers="headers"
                            :items="list"
                            :single-select="false"
                            fixed-header
                            height="600px"
                            item-key="index"
                            show-select
                            class="elevation-1"
                            multi-sort
                        >
                    </v-data-table>

                 <v-dialog v-model="dialog" persistent max-width="800px">
                    <popVoc :show="dialog"
                            :paramItem="paramItem"
                            @popAction = popAction>
                    </popVoc>
                </v-dialog>
            </v-flex>
        </v-layout>
    </v-container>
       
</template>

<script>
import btnBar from '../components/BtnBar.vue'
import popVoc from '../components/PopVoc.vue'
import vocList from '../dummy_json/vocList.json'


export default {
    components :{
        btnBar,
        popVoc
    },
    data : function(){
        return {
            //  
            dialog : false,
            isFail : false,
            prog : "P002",
            model:null,
            headers :[
                {text:"", width:80, value:'index'},
                {text:"",width:150,value:"category"},
                {text:"", width:150, value:"com_cd"},
                {text:"", width:150,value:"comp_name"},
                {text:"",width:150,value:"sol_gbn"},
                {text:"",width:150,value:"title"},
                {text:"",width:150,value:"text"},
                {text:"",width:150,value:"stat"},
                {text:"",width:150,value:"reg_id"},
                {text:"",width:150,value:"reg_date"},
            ],
            list :[],
            selected :[],
            paramItem :{}
        }
    },
    methods : {
        btnAction(btnId){
            switch (btnId) {
                case 'fadd':
                    this.paramItem = {}
                    this.dialog = true
                    break;
                case 'fsearch':
                    this.fsearch();
                    break;
                case 'fsave':
                    if(this.selected.length>0){
                        this.paramItem = this.selected[0]
                        this.dialog = true
                    }else{
                        this.isFail = true
                        setTimeout(()=>{this.isFail=false},2000)
                    }
                    break;
                case 'fexcel':
                    //this.exportexcel()
                    break;
                case 'finit':
                    this.list =[]
                    break
                default:
                    break;
            }
        },
        popAction(param){
            switch (param.id) {
                case 'popSave':
                    this.list.push(param.callback)
                    this.dialog = false
                    break;
                case 'popClose':
                    this.dialog = false
                    break;
            }

        },
        fsearch(){
            this.list = vocList
        }
    }
}
</script>